$theme-bg: #137bc1;
$link-color: #a9d8f7;
$link-active-bg: #158ad8;
$link-active-color: #fff;
$sub-bg: #116caa;

.sidebar {
  background-color: $theme-bg;
  border-right-color: transparent !important;
  .nav-link,
  .nav-link:focus:not(:hover):not(.active),
  .nav-sub ul a {
    color: $link-color;
  }
  .logo,
  .nav-link:hover,
  .nav-link.active,
  .nav-sub ul a:hover,
  .nav-sub ul a.active {
    color: $link-active-color;
  }
  .nav-link:hover,
  .nav-link.active {
    background-color: $link-active-bg;
  }
  .nav-sub ul {
    background-color: $sub-bg;
  }
  .nav-link.active {
    box-shadow: 3px 0 0 0 $link-active-color inset;
  }
}

.main-navbar {
  background-color: $theme-bg;
  [data-scroll],
  .navbar-nav .show > .nav-link,
  .navbar-nav .nav-link,
  .navbar-nav .nav-link:focus:not(:hover):not(.active) {
    color: $link-color;
    background-color: transparent;
  }
  [data-scroll]:hover,
  [data-scroll]:focus,
  .navbar-nav .nav-link:hover,
  .navbar-nav .nav-link.active {
    color: $link-active-color;
    background-color: $link-active-bg;
  }
  .navbar-nav .nav-link.active {
    box-shadow: 0 -1px $link-active-color inset;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
}
@media (min-width: 992px) {
  .sticky-navbar .main-header {
    box-shadow: none;
  }
}